.seats-title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  font-size: 30px;
  text-transform: uppercase;
  color: #292929;
  margin: 0;
  padding: 100px 0 0 85px;
}

.seats-select_departure, .seats-select_departure-back {
