  background: url(../../img/icons/css_sprites.png) -709px -520px;
  padding: 0 15px 0 0;
}

.travel-time_wrap p {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 18px;
  color: #000000;
  margin: 0;
}

.ticket-count-title, .wagon-type-title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  line-height: normal;
  font-size: 30px;
  color: #292929; 
  margin: 0;
  padding: 30px 0 0 30px;
}

.ticket-count-container {
  display: flex;
  padding-bottom: 50px;
  border-bottom: 1px solid #C4C4C4;
}

.ticket-count {
