.facilities-vector {
  width: 36px;
  height: 35px;
  border: 1px solid #292929;
  box-sizing: border-box;
  border-radius: 5px;
  margin-right: 10px;
}

.facilities-vector:hover {
  background-color: #FFA800;
  border-radius: 5px;
  border: 1px solid #292929;
}

.conditioner {
  background: url(../../img/icons/css_sprites.png) -80px -675px;
}

.wifi {
  background: url(../../img/icons/css_sprites.png) -626px -673px;
}

.linens {
  background: url(../../img/icons/css_sprites.png) -592px -543px;
}

.food {
  background: url(../../img/icons/css_sprites.png) -350px -544px;
}

.facilities-vector:hover.conditioner::after,.facilities-vector:hover.wifi::after,.facilities-vector:hover.linens::after, .facilities-vector:hover.food::after {
  display: flex;
  position: relative;
  top: 40px;
  left: -48px;
  justify-content: center;
  align-items: center;
  content: attr(data-title);
  width: 130px;
  height: 32px;
  background: #E5E5E5;
  border-radius: 5px;
  font-size: 16px;
  color: #292929;
  z-index: 1;
}

.conditioner-included {
  background: url(../../img/icons/css_sprites.png) -140px -674px;
}

.facilities-vector:hover.conditioner-included {
  background: url(../../img/icons/css_sprites.png) -80px -675px;
  background-color: #FFA800;
}

.wifi-included {
  background: url(../../img/icons/css_sprites.png) -685px -672px;
}

.facilities-vector:hover.wifi-included {
