  background-color: #FFA800;
}

.food-included {
  background: url(../../img/icons/css_sprites.png) -409px -543px;
}

.facilities-vector:hover.food-included {
  background: url(../../img/icons/css_sprites.png) -350px -544px;
  background-color: #FFA800;
}

