  display: flex;
  flex-direction: column;
  width: 921px;
  height: 228px;
  margin: 0 auto;
}

.standart {
  background-image: url(../../img/standart-wagon.png);
  background-repeat: no-repeat;
}

.seat-type {
  background-image: url(../../img/seat-wagon.png);
  background-repeat: no-repeat;
}

.seats-scheme ul {
  list-style: none;
  margin: 0;
  cursor: pointer;
}

.scheme_wagon-number {
  position: relative;
  left: 40px;
  width: 34px;
  height: 25px;
  background-color: #000000;
  color: #F7F6F6;
