  text-align: center;
  line-height: 25px;
}

.scheme_top-seats, .scheme_bottom-seats, .scheme_side-seats, .seat-type ul {
  display: flex;
  position: relative;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  line-height: 25px;
  padding-left: 129px
}

.scheme_side-seats {
