  background-color: #C4C4C4;
  color: #F7F6F6;
}

.available-place {
  width: 16px;
  background-color: #E5E5E5;
  color: #000000;
}

.available-place:hover {
  color: #FFA800;
}

.scheme_top-seats li {
  height: 29px;
  width: 26px;
  border-top: 4px solid #928f94d6;
}

.scheme_bottom-seats li {
  height: 29px;
  width: 26px;
  border-bottom: 5px solid #928f94d6;
}

.scheme_top-seats li:nth-child(odd), .scheme_bottom-seats li:nth-child(odd) {
  margin-right: 33px;
}

.scheme_top-seats li:nth-child(2n), .scheme_bottom-seats li:nth-child(2n) {
