}

.scheme_top-seats li:first-child, .scheme_bottom-seats li:first-child {
  border-left: 4px solid #928f94d6;
  margin-right: 35px;
}

.scheme_top-seats li:last-child, .scheme_bottom-seats li:last-child {
  margin-left: 3px;
}

